<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('AddProductMapping') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        ProductMappingId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "CustomSelectField",
                            mode: "tag",
                            label: this.$t('ProductName'),
                            name: "Product",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('SelectProductName'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "NumericField",
                            label: this.$t('Quantity'),
                            name: "Quantity",
                            value: "",
                            placeholder: this.$t('EnterQuantity'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
            ListData: []
        }
    },
    created: async function () {
        this.GetDdlRecords_Product();
    },
    methods: {
        GetDdlRecords_Product: async function () {
            var vm = this;
            var url = `WorkTypeId=${this.$route.params.id}`;
            await DataServices.GetProductByWorkTypeId(url).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.forEach((item, index) => {
                            vm.FormSchema[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        Close: function () {
            this.$parent.AddProductMapping = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var FSMProductList = '';
            var i = 0;
            for (i = 0; i < formfields.Product.length; i++) {
                if (i == 0) {
                    FSMProductList = formfields.Product[i].value;
                } else {
                    FSMProductList = FSMProductList + "," + formfields.Product[i].value;
                }
            }
            var jsonobj = {
                Id: vm.ProductMappingId == null ? 0 : vm.ProductMappingId,
                FSMProductList: FSMProductList,
                WorkTypeId: this.$route.params.id,
                Quantity: formfields.Quantity
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveWorkTypeProductMapping(params).then((response) => {
                if (response.data.status == 'Success') {
                    if (vm.$parent.ProductMappingId > 0) {

                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Product")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Product")]), "success", true, vm.$t("Alert"));
                    }
                    vm.Close();
                    vm.$parent.FetchRecords();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Resource")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>