<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('AddFormMapping') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "CustomSelectField",
                            mode: "tag",
                            label: this.$t('Form'),
                            name: "Form",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('SelectForm'),
                            validationRules: { "required": true },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Subject'),
                            name: "Subject",
                            value: "",
                            placeholder: this.$t('EventSubject'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('NotificationTo'),
                            name: "NotificationTo",
                            value: "",
                            placeholder: this.$t('EMAIL'),
                            validationRules: "",
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "toggleField",
                            label: this.$t('IsFormMandatory'),
                            name: "IsFormMandatory",
                            value: "",
                            placeholder: this.$t('EnterDescription'),
                            validationRules: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('IsApprovalRequired'),
                            name: "IsApprovalRequired",
                            value: "",
                            placeholder: this.$t('EnterDescription'),
                            validationRules: "",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ]
        }
    },
    created: async function () {
        this.GetDdlRecords_Form();
    },
    methods: {
        GetDdlRecords_Form: async function () {
            var vm = this;
            var url = `id=${this.$route.params.id}&&moduleName=${'FSM'}&type=${'FSM_FORMS_FOR_NONLOCATE'}&search=&refId=`;
            await DataServices.GetDDLData(url).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        Close: function () {
            this.$parent.AddFormMapping = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var FSMFormList = '';
            var i = 0;
            for (i = 0; i < formfields.Form.length; i++) {
                if (i == 0) {
                    FSMFormList = formfields.Form[i].value;
                } else {
                    FSMFormList = FSMFormList + "," + formfields.Form[i].value;
                }
            }
            var jsonobj = {
                selectedStatusCode: 0,
                selectedForm: FSMFormList,
                subject: formfields.Subject,
                notificationTo: formfields.NotificationTo,
                isApprovalRequired: formfields.IsApprovalRequired,
                isFormMandatory: formfields.IsFormMandatory,
                workTypeId: this.$route.params.id,
                sub_status_code_id: 0
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveFormMapping(params).then((response) => {
                if (response.data.status == 'Success') {
                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Form")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.$parent.FetchRecords();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Resource")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>