<template>
    <section>
        <tg-list :IsShowAction="false" ModuleName="Ticketing" SubModuleCode="SERVICE_APPOINTMENT" :LegendArray="[]"
            :RenderRowActionMethod="() => { }" :widgets="[]" IdentityColumn="MAPPING_ID"
            :HeaderText="$t('ServiceAppointment')" :listType="['List']" :ListData="FormData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" 
            :ListDataCallBackFunction="FetchRecords" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'IS_FORM_MANDATORY'">
                    <div class="form-group mb-0">
                        <label class="switch disabled">
                            <input class="form-check-input custom-control-input dynamic" type="checkbox"
                                v-model="data.row.IS_FORM_MANDATORY" v-bind:name="'IsLogin'" v-disabled="disabled">
                            <span class="slider round" style="cursor:default;">
                                <span class="slider-yes">Yes</span>
                                <span class="slider-no">No</span>
                            </span>
                        </label>
                    </div>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'IS_APPROVAL_REQUIRED'">
                    <div class="form-group mb-0">
                        <label class="switch disabled">
                            <input class="form-check-input custom-control-input dynamic" type="checkbox"
                                v-model="data.row.IS_APPROVAL_REQUIRED" v-bind:name="'IsLogin'" v-disabled="disabled">
                            <span class="slider round" style="cursor:default;">
                                <span class="slider-yes">Yes</span>
                                <span class="slider-no">No</span>
                            </span>
                        </label>
                    </div>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'Action'">
                    <td class="text-center single-action tourguide_aa">
                        <a v-on:click="DeleteById(data.row.MAPPING_ID)" :typetitle="$t('Delete')" typefor="Delete"
                            v-bind:dataid="data.row.MAPPING_ID" :title="$t('Delete')">
                            <em class="fa fa-trash text-danger action-icon"></em>
                        </a>
                    </td>
                </template>
            </template>
        </tg-list>
        <AddFormMappingModal v-if="AddFormMapping"></AddFormMappingModal>
    </section>
</template>
<script>
import Schema from "@/views/crew/CrewResourceSchema";
import AddFormMappingModal from "../../../components/Modal/AddFormMappingModal.vue";
import DataServices from "@/services/DataService";
export default {
    components: { AddFormMappingModal },
    data() {
        return {
            AddFormMapping: false,
            ProductMappingId: 0,
            noRecords: false,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SearchCondition: '',
            Headers: [{
                "COLUMN_NAME": "FORM_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Form Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "IS_FORM_MANDATORY",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Is Form Mandatory",
                "DISPLAY_ORDER": 2,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "IS_APPROVAL_REQUIRED",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Is Approval Required",
                "DISPLAY_ORDER": 3,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "Action",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Action",
                "DISPLAY_ORDER": 3
            },
            ],
            listheaderbuttons: Schema.buttons,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            user_id: null,
            companyId: null,
            leftSearchFields: null,
            FormData: []
        }
    },
    created: async function () {        
        var vm = this;
        this.user_id = this.GetUserInfo.ID
        this.user_type = this.GetUserInfo.UserType
        this.companyId = this.GetUserInfo.CompanyId;
        await vm.FetchRecords();
    },
    methods: {
        leftCommonSearch(){},
        FetchRecords: async function () {
            var vueObj = this;
            var url = `WtId=${this.$route.params.id}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            await DataServices.FsmGetFormMappingByWorkType(url).then(
                function (response) {
                    vueObj.TotalRecords = 0;
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "IS_FORM_MANDATORY") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "IS_APPROVAL_REQUIRED") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "Action") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    vueObj.FormData = [];
                    if (response.data != "" && response.data != null) {
                        if (response.data.status == "Success") {
                            if (response.data.result.data.length > 0) {
                                vueObj.FormData = response.data.result.data;
                                vueObj.TotalRecords = vueObj.FormData[0].TOTALRECORDS;
                                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                                vueObj.CurrentPage = vueObj.PageNumber;
                                $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                                $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                                vueObj.noRecord = false;
                            }
                        }
                        else {
                            vueObj.noRecord = true;
                            vueObj.FormData = [];
                            vueObj.TotalRecords = 0
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.ProductData = [];
                        vueObj.Headers = [];
                        vueObj.TotalRecords = 0
                    }
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                });
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        openPopupForAddFormMapping: function (id) {
            if (id == undefined)
                id = 0;
            this.AddFormMapping = !this.AddFormMapping;
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "CommonAddData":
                    this.openPopupForAddFormMapping(null);
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "BackToList":
                    this.BacktoWorktypeList();
                    break;
            }
        },
        DeleteById: function (id) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Ticketing Service Board")), function () {
                var postJSON = {
                    ids: id,
                    companyId: vm.companyId,
                    userId: vm.user_id,
                    moduleName: "FSM",
                    refCode: "TALYGEN_FSM_STATUS_CODE_FORM_MAPPING"
                };
                var objectPostString = JSON.stringify(postJSON);
                DataServices.deleteActivityData(objectPostString).then(response => {
                    if (response.data[0].Status == "Success") {
                        vm.FetchRecords();
                        setTimeout(function () {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('FormMapping')]), "Success", true, vm.$t('Alert'));
                        }, 500);
                    }
                    else {
                        vm.ShowAlert(vm.$t('DeletedError', [vm.$t('FormMapping')]), "failure", true, vm.$t('Alert'));
                    }

                });
            });
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Ticketing Service Board")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "FSM",
                        refCode: "TALYGEN_FSM_STATUS_CODE_FORM_MAPPING"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        if (response.data[0].Status == "Success") {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.FetchRecords();
                            setTimeout(function () {
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('FormMapping')]), "Success", true, vm.$t('Alert'));
                            }, 500);
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('FormMapping')]), "failure", true, vm.$t('Alert'));
                        }

                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        BacktoWorktypeList: function () {
            this.$router.push({ name: 'WorkTypeListing' });
        }
    },
}
</script>